<template>
    <yd-layout class="box_danger_recheck">
        <navBar title="隐患复查" slot="navbar"></navBar>
        <div class="box_contc">
            <div class="box_titlec">整改信息</div>
            <div class="flex"><span class="gray">整改金额：</span>{{zgmoney}}元</div>
            <div class="flex"><span class="gray">整改人员：</span>{{zgname}}</div>
            <div class="flex"><span class="gray">整改时间：</span>{{zgtime}}</div>
            <div class="flex"><span class="gray">整改备注：</span><div style="flex: 1;">{{zgremark}}</div></div>
            <div class="flex">
                <div class="gray">整改图片：</div>
                <div class="flex_image">
                    <div class="flex_image_img" @click="onPressLightbox($event,zgimage)">
                        <img :src="zgimage" alt="">
                    </div>
                </div>
            </div>
        </div>
        <div class="box_form">
            <div class="box_titlec" style="color: orange;">复查信息</div>
            <div class="box_cont" style="align-items: center;">
                <p class="mright">金额：</p>
                <input v-on:focus="heighOn" type="number" class="box_input" placeholder="请输入整改金额(元)" v-model="money">
            </div>
            <div class="box_cont mtop">
                <p class="mright">备注：</p>
                <textarea v-on:focus="heighOn" rows="5" class="box_txt" placeholder="请输入备注" v-model="remark"></textarea>
            </div>
            <div class="box_cont mtop">
                <p class="mright">附件：</p>
                <unphoto v-on:imagesUrl="saveUrl"></unphoto>
            </div>
            <div class="box_cont mtop">
                <p class="mright">状态：</p>
                <yd-radio-group v-model="state" color="#1D91E6">
                    <yd-radio val="0">复查结束</yd-radio>
                    <yd-radio val="1">重新整改</yd-radio>
                </yd-radio-group>
            </div>
            <yd-button @click.native="saveData" size="large" color="#fff" bgcolor="#1D91E6">保存</yd-button>
        </div>
    </yd-layout>
</template>

<script>
    import unphoto from '../../components/unphoto.vue'
    import {api_xunjian_recheck} from '../../components/APIKeys'
    export default {
        name: "DangerRecheck",
        components:{unphoto},
        data(){
            return{
                // token:window.localStorage.getItem('token'),
                info:JSON.parse(window.localStorage.getItem('danger')),
                remark:'',//复查备注
                money:null,//复查金额
                photoUrl:'',//复查图片
                state:1,
                zgmoney:null,//整改金额
                zgname:'',//整改人员
                zgremark:'',//整改备注
                zgimage:'',//整改图片
                zgtime:'',//整改时间
                yhclId:'',//隐患处理ID
            }
        },
        mounted(){
          console.log('本条信息',this.info);
          this.checkState();

        },
        methods:{
            checkState(){
                let history = this.info.history;
                  for(let i = 0 ;i < this.info.history.length; i++){
                     if(history[i].yhclzt == 0){
                         this.zgmoney =  history[i].yhzgje;//整改金额
                         this.zgname =  history[i].yhzgr;//整改人员
                         this.zgremark =  history[i].yhzgbz;//整改备注
                         this.zgimage =  history[i].yhzgzp;//整改照片
                         this.zgtime =  history[i].yhzgsj;//整改照片
                         this.yhclId =  history[i].yhcl_id;//隐患处理ID
                     }
                  }
            },
            //将子组件的值传过来
            saveUrl:function(data){
                this.photoUrl = data;
            },
            //点击保存
            saveData(){
                if(this.money == null){
                    this.$showError('请填写复查金额');
                    return false;
                }else if(this.remark == ''){
                    this.$showError('请填写复查备注');
                    return false;
                }else if(this.photoUrl.length == 0){
                    this.$showError('请上传复查图片');
                    return false;
                }
                this.$showDialog();
                let jsonData = {
                    // token:this.token,
                    yhjl_id:this.info.yh_id,
                    yhcl_id:this.yhclId,
                    yhfcje:this.money,
                    yhfcbz:this.remark,
                    yhfczp:this.photoUrl,
                    state:this.state,
                }
                console.log('提交的数据',jsonData);
                this.$post(api_xunjian_recheck, jsonData).then(res =>{
                    this.$showDialog(false);
                    if(res.errno == 0){
                        this.$showSuccess('保存成功');
                        window.history.go(-2);
                    }else{
                        this.$showError('保存失败');
                    }
                })
            },
            //防止键盘遮挡
            heighOn: function() {
                if (/Android/gi.test(navigator.userAgent)) {
                    window.addEventListener('resize', function () {
                        if (document.activeElement.tagName == 'INPUT' || document.activeElement.tagName == 'TEXTAREA') {
                            window.setTimeout(function () {
                                document.activeElement.scrollIntoViewIfNeeded();
                            }, 0);
                        }
                    })
                }
            },
        }
    }
</script>

<style scoped>
    .box_contc{
        margin:.24rem;
        padding: .24rem;
        background: #fff;
    }
    .box_titlec{
        font-size: .34rem;
        margin-bottom: .1rem;
        /*border-bottom: 1px solid #d9d9d9;*/
    }
    .gray{
        color: #909090;
    }
    .box_correct_img{
        height: 3rem;
    }
    .box_correct_img img{
        width: 100%;
        height: 100%;
    }
    .box_form{
        padding: .24rem;
        margin: .24rem;
        background: #fff;
    }
    .box_cont{
        display: flex;
        /*align-items: center;*/
    }
    .box_input{
        height: 0.8rem;
        flex: 1;
        border: 1px solid #d9d9d9;
        padding: .2rem;
        border-radius:5px;
    }
    .box_txt{
        flex: 1;
        border: 1px solid #d9d9d9;
        border-radius: 5px;
        padding: .2rem;
    }
    .mtop{
        margin-top: .2rem;
    }
    .mright{
        margin-right: .2rem;
        white-space:nowrap;
    }
    .flex{
        display: flex;
    }
    .flex_image{
        flex: 1;
        display: flex;
        align-items: center;
    }
    .flex_image_img{
        width: calc(107% / 3);
        height: 1.8rem;
    }
    .flex_image_img img{
        width: 100%;
        height: 100%;
    }
</style>
<style>
    .box_danger_recheck .yd-radio>input[type=radio]:checked+.yd-radio-icon{
        margin-bottom: .03rem !important;
    }
    .box_danger_recheck .yd-radio-icon{
        margin-bottom: .03rem;
    }
    .box_danger_recheck .yd-radio{
        padding-right: 1rem;
    }
</style>



